:root{
    #root{
        position: fixed;
        width: 100vw!important;
        height: 100vh!important;
    }
    body,html {
        font-family: Roboto;
        margin: 0;
        padding: 0;
        width: 100vw!important;
        height: 100vh!important;
    }
    * {
        box-sizing: border-box;
    }
    img{
        object-fit: contain;
    }
}

@import url('https://fonts.googleapis.com/css?family=Roboto:300,400,500,900,200,100,600,800,bold,medium,normal,light,lighter,700&display=swap');
